<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <div class="text-right">
      <button class="btn btn-primary text-light mt-4" data-toggle="modal" @click="openCouponModal(true)"></button>
    </div>
    <table class="table mt-4 table-responsive-md table-hover">
      <thead class="thead-light">
        <th></th>
        <th></th>
        <th></th>
        <th> (%)</th>
        <th></th>
        <th width="150"></th>
      </thead>
      <tbody>
        <tr v-for="item in coupons" :key="item.id">
          <td>{{ item.title }}</td>
          <td>{{ item.code }}</td>
          <td>{{ item.due_date }}</td>          
          <td>{{ item.percent }}</td>          
          <td>
            <span v-if="item.is_enabled" class="text-success"></span>
            <span v-else class="text-secondary"></span>
          </td>
          <td>
            <button class="btn btn-outline-primary btn-sm" @click="openCouponModal(false, item)"></button>
            <button class="btn btn-outline-secondary btn-sm" @click="openDeleteModal(item)"></button>
          </td>
        </tr>
      </tbody>
    </table>
    <Pagination :page-data="pagination" @render="getCoupons"></Pagination>
    <!-- Modal -->
    <div class="modal fade" id="couponModal" tabindex="-1" role="dialog" aria-labelledby="couponModalLabel" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="couponModalLabel"></h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-6">
                <div class="form-group">
                  <label for="title"></label>
                  <input type="text" class="form-control" id="title" placeholder="" v-model="tempCoupon.title">
                </div>
              </div>
              <div class="col-6">
                <div class="form-group">
                  <label for="percent"> %</label>
                  <input type="number" class="form-control" id="percent" placeholder="" v-model="tempCoupon.percent">
                </div>
              </div>      
            </div>
            <div class="row">
              <div class="col-6">
                <div class="form-group">
                  <label for="due_date"></label>
                  <input type="date" class="form-control" id="due_date" placeholder="" v-model="tempCoupon.due_date">
                </div>
              </div>
              <div class="col-6">
                <div class="form-group">
                  <label for="code"></label>
                  <input type="text" class="form-control" id="code" placeholder="" v-model="tempCoupon.code">
                </div>
              </div>      
            </div>
            <div class="form-group text-right">
              <div class="form-check">
                <input class="form-check-input" type="checkbox" id="is_enabled"  v-model="tempCoupon.is_enabled" :true-value="1" :false-value="0">
                <label class="form-check-label" for="is_enabled"></label>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-primary" @click="updateCoupon"></button>
          </div>
        </div>
      </div>
    </div>
    <!-- Modal  -->
    <div class="modal fade" id="delCouponModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-danger text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
             <strong class="text-danger">{{ tempCoupon.title }}</strong> 
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-danger" @click="deleteCoupon"></button>
          </div>
        </div>
      </div>
    </div>

  </div>
</template>


<script>
import $ from "jquery";

export default {
  name: "Coupons",
  data() {
    return {
      coupons: [],
      tempCoupon: {},
      pagination: {},
      isNew: false,
      isLoading: false
    };
  },
  methods: {
    getCoupons() {
      const api = `${process.env.APIPATH}/api/${
        process.env.CUSTOMPATH
      }/admin/coupons`;
      const vm = this;

      this.isLoading = true;

      this.$http.get(api).then(response => {
        // console.log(response.data);
        vm.isLoading = false;
        vm.coupons = response.data.coupons;
        vm.pagination = response.data.pagination;
      });
    },
    openCouponModal(isNew, item) {
      if (isNew) {
        this.tempCoupon = {};
        this.isNew = true;
      } else {
        this.tempCoupon = Object.assign({}, item);
        this.isNew = false;
      }
      $("#couponModal").modal("show");
    },
    updateCoupon(page = 1) {
      const vm = this;
      let api = `${process.env.APIPATH}/api/${
        process.env.CUSTOMPATH
      }/admin/coupon?page=${page}`;
      let method = "post";

      if (!this.isNew) {
        api = `${process.env.APIPATH}/api/${
          process.env.CUSTOMPATH
        }/admin/coupon/${vm.tempCoupon.id}`;
        method = "put";
      }

      this.$http[method](api, { data: vm.tempCoupon }).then(response => {
        // console.log(response.data);
        if (response.data.success) {
          $("#couponModal").modal("hide");
          this.$bus.$emit("alertMessage", response.data.message, "success");
          vm.getCoupons();
        } else {
          $("#couponModal").modal("hide");
          this.$bus.$emit("alertMessage", response.data.message, "danger");
          vm.getCoupons();
        }
      });
    },
    openDeleteModal(item) {
      this.tempCoupon = Object.assign({}, item);
      $("#delCouponModal").modal("show");
    },
    deleteCoupon() {
      const vm = this;
      const api = `${process.env.APIPATH}/api/${
        process.env.CUSTOMPATH
      }/admin/coupon/${vm.tempCoupon.id}`;

      this.$http.delete(api).then(response => {
        if (response.data.success) {
          $("#delCouponModal").modal("hide");
          this.$bus.$emit("alertMessage", response.data.message, "success");
          vm.getCoupons();
        } else {
          $("#delCouponModal").modal("hide");
          this.$bus.$emit("alertMessage", response.data.message, "danger");
          vm.getCoupons();
        }
      });
    }
  },
  created() {
    this.getCoupons();
  }
};
</script>



